.user-card .dz-media {
  height: 80px;
  width: 80px;
  min-width: 80px;
  border-radius: 10px;
  overflow: hidden;
  border: 4px solid #fff;
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.1);
}

.user-card .dz-media img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.user-card .icon-placeholder {
  font-size: 24px;
  text-transform: uppercase;
  line-height: 70px;
  font-weight: 500;
  display: block;
  letter-spacing: 2px;
  text-align: center;
  height: 100%;
}

.user-card .title a {
  color: #000;
}

.user-card .title a:hover {
  color: #004475;
}

.user-card .list-group {
  margin: 0 -30px;
}

.user-card .list-group .list-group-item {
  padding: 10px 30px;
}

.profile-wrapper .profile-img {
  height: 150px;
  width: 150px;
  min-width: 150px;
  display: inline-block;
  border-radius: 0.35rem;
}

.profile-wrapper .profile-img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 0.35rem;
}

.profile-wrapper .profile-img.active {
  position: relative;
}

.profile-wrapper .profile-img.active:after {
  content: "";
  position: absolute;
  right: -5px;
  top: -5px;
  border-radius: 50%;
  border: 3px solid #fff;
  height: 18px;
  width: 18px;
  background: #004475;
}

.profile-wrapper .user-list-detail {
  display: flex;
}

.profile-wrapper .user-list-detail li {
  padding-right: 12px;
  margin-right: 12px;
  border-right: 1px solid #eeeeee;
}

.profile-wrapper .user-list-detail li:last-child {
  border-right: 0;
}

.profile-wrapper .user-list-detail li svg {
  width: 20px;
  fill: rgba(0, 23, 55, 0.08);
}

.user-states-info {
  background: #f7f7f7;
  border-radius: 0.35rem;
  text-align: center;
  padding: 15px 10px;
  border: 1px solid #dedede;
  border-style: dashed;
}

.user-states-info .user-states-info-name {
  font-size: 16px;
  color: black;
  font-weight: 600;
  display: block;
}

.user-states-info .user-states-value {
  font-size: 15px;
}

.default-progress {
  height: 20px;
}

.default-progress .progress-bar {
  border-radius: 20px;
}

.default-progress .progress-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  padding: 0 15px;
}

.bg-gradient-1 {
  background: #ac39d4;
  background: -moz-linear-gradient(left, #ac39d4 0%, #f04cf3 100%);
  background: -webkit-linear-gradient(left, #ac39d4 0%, #f04cf3 100%);
  background: linear-gradient(to right, #ac39d4 0%, #f04cf3 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ac39d4', endColorstr='#f04cf3', GradientType=1);
}

