.dz-demo-panel {
  right: -100%;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100vh;
  transition: all 0.5s ease-in-out;
  z-index: 9999;
}

.dz-demo-panel .dz-demo-trigger {
  position: absolute;
  z-index: 9;
  top: 15.75rem;
  right: 100%;
  background-color: #6418c3;
  color: #fff;
  display: inline-block;
  height: 3rem;
  width: 3rem;
  text-align: center;
  font-size: 1.75rem;
  line-height: 3rem;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  box-shadow: -5px 3px 5px 0px rgba(119, 119, 119, 0.15);
}

@media only screen and (max-width: 1023px) {
  .dz-demo-panel .dz-demo-trigger {
    display: none;
  }
}

@media only screen and (max-width: 35.9375rem) {
  .dz-demo-panel .dz-demo-trigger {
    height: 35px;
    width: 35px;
    line-height: 35px;
    font-size: 22px;
  }
}

.dz-demo-panel .dz-demo-close {
  height: 30px;
  color: #000;
  width: 30px;
  border-radius: 0.35rem;
  background: #fff;
  line-height: 30px;
  text-align: center;
}

.dz-demo-panel.show {
  right: 0;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.2);
  z-index: 99999;
  overflow: hidden;
}

.dz-demo-panel.show .sidebar-right-trigger {
  display: none;
}

.dz-demo-panel.show .bg-close {
  position: fixed;
  z-index: -2;
  cursor: pointer;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
}

.dz-demo-panel .dz-demo-inner {
  padding: 30px 30px;
  background: #2b0000a1;
}

.dz-demo-panel .dz-demo-content {
  height: calc(100vh - 200px);
  padding: 0 15px;
}

.dz-demo-panel .dz-demo-content .text-black {
  color: #000 !important;
}

.dz-demo-panel .dz-demo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.dz-demo-panel .dz-demo-header h4,
.dz-demo-panel .dz-demo-header .h4 {
  margin-bottom: 0;
  color: #000 !important;
}

.dz-demo-panel .dz-demo-bx {
  height: 300px;
  overflow: hidden;
  border: 5px solid #efefef;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 10px;
}

.dz-demo-panel .dz-demo-bx.demo-active {
  border-color: #6418c3;
}

.dz-demo-panel .dz-demo-bx.demo-active .overlay-layer {
  opacity: 1;
}

.dz-demo-panel .overlay-bx {
  position: relative;
}

.dz-demo-panel .overlay-bx .overlay-layer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  opacity: 0;
}

.dz-demo-panel .overlay-bx:hover .overlay-layer {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  opacity: 1;
}

@media only screen and (max-width: 35.9375rem) {
  .dz-demo-panel .dz-demo-bx {
    height: 230px;
  }
}

